      <div :class="activeName === '' ? 'active' :''" class="middleItem" @click="clickLink('')"></div>
      <div :class="activeName === 'indexDir_2' ? 'active' :''" class="middleItem" @click="clickLink('indexDir_2')"></div>
    </aside> -->
